const windowProxy = new Proxy(Window, {
  get(target, prop, receiver) {
    if (prop === "location") {
      return window.__location;
    } else if (typeof prop === "string" && ["self", "globalThis", "window", "parent", "opener"].includes(prop)) {
      return this;
    } else {
      return target[prop];
    }
  },
});

window.__window = windowProxy;
